/*custom radio*/
.options-radio {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 18px;
    font-weight: 500;
    color: #fff;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
 margin-right: 50px;
}

/* Hide the browser's default checkbox */
.options-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 1px;
    left: 0;
    height: 26px;
    width: 26px;
    border: 2px solid #EC3525;
    border-radius: 50%;
    background: #fff;
}



/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.options-radio input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.options-radio .checkmark:after {
  left: 3px;
  top: 3px;
  width: 16px;
  height: 16px;
  background: #EC3525;
    border-radius: 50%;
  
}
