}
.streming-head-right ul li{
    list-style-type: none;
    margin: 0;
    display: inline-block;
    padding: 0;
}
.streming-head-right ul li a{
    font-size: 16px;
    color: #fff;
    padding: 13px 22px;
    display: block;
    text-decoration: none;
    line-height: 24px;
}
.streming-head-right ul li a.active{
    background: #EC3525;
    color: #fff;
}

.start-stream-btn{
    width: 187px;
    height: 50px;
    border-radius: 25px;
    background: #484848;
