}
.strame-chat-section{
    display: flex;
    justify-content: space-between;
}
.strame-main{
    width: 65%;
    min-height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #333333;
    text-align: center;
}
.strame-main h3{
    font-weight: 600;
    font-size: 22px;
    color: #fff;
    margin: 0;
    padding: 0;
    line-height: 35px;
}

.strame-main p{
    font-weight: normal;
    font-size: 16px;
    color: #fff;
    margin: 0;
    line-height: 28px;
    padding: 0;
}
.chat-main{
    width: 33%;
    background: #333333;
    padding: 20px 30px;
    position: relative;
}
.chat-main-input{
    position: absolute;
    left: 4%;
    bottom: 15px;
    width: 92%;
}
.chat-main-input .input-field{
    background: #484848;
    border: 1px solid #2C2C2C;
    border-radius: 10px;
    background: #484848;
    height: 60px;
    width: 100%;
    font-size: 16px;
    color: #fff;
    outline: 0;
    padding: 0 40px 0 15px;
}
.chat-main-input .input-field::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}
.chat-main-input .input-field:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}
.chat-main-input .input-field::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}
.chat-main-input .input-field::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}

.chat-main-input .input-field:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #fff;
  opacity: 1;
}

.chat-main-input .input-field::-ms-input-placeholder { /* Microsoft Edge */
  color: #fff;
  opacity: 1;
}
.chat-main h4{
    color: #EC3525;
    font-weight: 600;
